<template>
  <table class="offer-create-table" v-if="!$isMobile">
    <thead>
      <tr>
        <th>Артикул</th>
        <th>Наименование</th>
        <th>Цена</th>
        <th>Кол-во</th>
        <th>Скидка</th>
        <th>Стоимость</th>
        <th></th>
      </tr>
    </thead>
    <tbody v-if="products.length">
      <tr v-for="(p, i) in products" :key="i">
        <td>
          <span>
            {{ p.article }}
          </span>
          <span class="tooltip" v-tooltip>
            {{ p.article }}
          </span>
        </td>
        <td>
          <span>
            {{ p.name }}
          </span>
          <span class="tooltip" v-tooltip>
            {{ p.name }}
          </span>
        </td>
        <td class="no-wrap">
          <base-input
            :type="'text'"
            :tableInput="true"
            :symbol="'₽'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.price"
          />
        </td>
        <td class="no-wrap">
          <base-input
            :type="'number'"
            :tableInput="true"
            :symbol="'шт'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.count"
          />
        </td>
        <td class="no-wrap">
          <base-input
            :type="'number'"
            :tableInput="true"
            :symbol="'%'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.discount"
          />
        </td>
        <td>
          <span> {{ p.cost }} ₽ </span>
          <span class="tooltip" v-tooltip> {{ p.cost }} ₽ </span>
        </td>
        <td class="no-wrap">
          <span
            class="_remove"
            @click="
              () => {
                $emit('removeProduct', p.id);
              }
            "
          >
            <img src="@/assets/icons/clear.svg" alt="Remove" />
          </span>
        </td>
      </tr>
    </tbody>
  </table>
  <div
    class="offer-create-table-mobile table-mobile"
    ref="table"
    v-else-if="products.length && $isMobile"
  >
    <div class="table-mobile-column" v-for="(p, i) in products" :key="i">
      <span
        class="_remove"
        @click="
          () => {
            $emit('removeProduct', p.id);
          }
        "
      >
        <img src="@/assets/icons/clear.svg" alt="Remove" />
      </span>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Артикул </span>
        <span class="table-mobile__value">
          {{ p.article }}
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Наим-ие </span>
        <span class="table-mobile__value">
          {{ p.name }}
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Цена </span>
        <span class="table-mobile__value">
          <base-input
            :type="'text'"
            :tableInput="true"
            :symbol="'₽'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.price"
          />
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Кол-во </span>
        <span class="table-mobile__value">
          <base-input
            :type="'number'"
            :tableInput="true"
            :symbol="'шт'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.count"
          />
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Скидка </span>
        <span class="table-mobile__value">
          <base-input
            :type="'number'"
            :tableInput="true"
            :symbol="'%'"
            @blur="$emit('editProduct', p.id)"
            v-model="p.discount"
          />
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Стоимость </span>
        <span class="table-mobile__value"> {{ p.cost }} ₽ </span>
      </div>
    </div>
  </div>
  <div class="offer-create-price" v-if="products.length">
    <div>
      <div class="offer-create-price__discount">
        <span> Скидка </span>
        <span> {{ +totalDiscountedPrice.toFixed(2) }} ₽ </span>
      </div>
      <div class="offer-create-price__total">
        <span> Общая стоимость </span>
        <span> {{ (+totalPrice - +totalDiscountedPrice).toFixed(2) }} ₽ </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "OfferCreateTable",
  props: {
    products: {
      type: Array,
    },
  },
  computed: {
    // get discounted price
    totalDiscountedPrice() {
      return (
        this.products.length &&
        this.products.reduce(
          (prev, next) =>
            (+prev.cost * +prev.discount) / 100 +
            (+next.cost * +next.discount) / 100
        )
      );
    },
    // get total price
    totalPrice() {
      return (
        this.products.length &&
        this.products.reduce((prev, next) => +prev.cost + +next.cost)
      );
    },
  },
  emits: ["removeProduct", "editProduct"],
};
</script>

